.sidebar {
	flex: 1;
}
.sidebarWrapper {
	padding: 20px;
	color: #555;
}
.sidebarMenu {
	margin-bottom:  2rem;
}
.sidebarTitle {
	font-size:  0.9rem;
	color: rgb(197, 197, 197);
}
.sidebarList {
	list-style: none;
	padding: 0.5rem;
}
.sidebarListItem {
	padding: 5px;
	cursor: pointer;
	display: flex;
	align-items: center;
	border-radius: 10px;
}
.sidebarListItem.active,
.sidebarListItem:hover {
	background-color: rgb(212, 209, 235);
}
.sidebarIcon {
	margin-right: 10px;
	font-size: 20px !important;
}